const _hoisted_2 = /*#__PURE__*/_createVNode("p", null, " This will be more advanced topic for the Web-Developer. Vuex is a state management pattern for Vue.js applications. This pattern is build around the concept of \"one-way data flow\" where View components get data from the Store and update the state by dispatching specific actions that mutate the Store state. In this pattern the \"state\" is the single source of truth that handles all the data. View components can map the state to the local variables and display the data. This is a very powerful pattern where we need to change the value in one place and the change will be reflected in all the other places. ", -1)
const _hoisted_3 = /*#__PURE__*/_createVNode("p", null, " OK. But why? Well, the answer is simple. If our application is growing larger and larger, and the components relationships are becoming more complex, we need to make sure that the valid data is being shared between the components. This is where Vuex comes in. We don't need to worry about the passing props and synchronizing the data between the components. Vuex store can be treated as a global singleton that holds the state of whole application. Dan Abramov, the author of Redux said that: \"Flux libraries are like glasses: youll know when you need them.\" ", -1)
