const _hoisted_7 = /*#__PURE__*/_createStaticVNode("<p> The Vuex object must be attached to the global Vue instance. Base on that we can access the store from any Vue component by using the <code>this.$store</code> property. The dollar sign is a convention to access the property of the global Vue instance. </p><p> As we know the Vue.js framework must have the initial point where the application is attached. So somewhere in the <code>main.js</code> file we can create this store instance or import it from the separate file. There we should place this <code>Vue.use()</code> method. </p><p> Ok. Now we have our Vuex store object. We have defined there a <code>state</code> property which will be responsible for holding the state of the application. By default the Vuex Store use reactive data structures. This means that the value of an object is automatically updated when the state changes. This is the reason why we can&#39;t change the state directly. To change it we need to commit and mutation that will perform some operation on the state. </p><p> In our case we have the <code>items</code> array that holds the list of items in our shopping cart. Only way how we can update this array is using the <code>addItem()</code> mutation. To do that we must use our store reference and call the <code>commit()</code> method. As the first argument we must pass the name of the mutation. The second argument is the payload that will be passed into it. The example below shows how to add an book item to the shopping cart. </p><label>BookItem.vue</label>", 5)
const _hoisted_12 = /*#__PURE__*/_createStaticVNode("<p> Well, we added a new item to the shopping cart but we need to know what is inside our basket. That will be very easy because as I said before the Store object is assigned to the global Vue instance so we can access it from any Vue component. Using the <code>this.$store.state</code> property we can access the state of the store and then we can access the <code>items</code> array. So we can get the list of items in the shopping cart using this <code>this.$store.state.items</code> code. </p><p> Nice, but still not perfect. Sometimes we will have a nested object that we need to access and to solve this problem we can use the <code>computed</code> property of the Vue component. Using it we can simplify the code and make it more readable. What is more we can modify the items array before we use it by adding some additional logic to the <code>computed</code> property. But this time we will only create a <code>basket</code> computed property that will return the list of items that are in the shopping cart. </p><label>Basket.vue</label>", 3)
